.eg-grid {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;

  @include mq(sm) {
    display: grid;
    gap: 1.1rem;
    grid-template-columns: repeat(4, 1fr);
  }

  &.no-padding {
    padding-left: 0;
    padding-right: 0;
  }

  &--content {
    grid-template-columns: 0.5fr 2fr 0.5fr;
  }
}

.eg-grid__content {
  display: grid;
  gap: 1.1rem;
  height: 100%;
}

.eg-grid__side {
  display: grid;
  gap: 1.1rem;
  grid-template-columns: 1fr;
  height: 100%;
}

// span COLUMNS

[class^="eg-cell"],
