.eg-gallery {
  max-width: 100%;
  margin-bottom: 2rem;

  // .slick-track {
  //   max-width: 100%;
  //   height: auto !important;
  // }

  // .slick-slide {
  //   width: 100%;
  //   height: auto;
  // }

  &__nav {
    padding: 0 1rem;
    display: flex;
    gap: 1rem;
    border-bottom: 3px solid $mainColor;
  }

  &__nav-button {
    height: 100%;
    position: relative;
    font-family: $altFont;
    font-size: 1.5rem;
    text-transform: uppercase;
    padding: .5rem 1rem;
    background: none;
    border: none;
    background: linear-gradient(to top, $egYellow 0%, $white 0%);
    @include trans;
    cursor: pointer;

    &.js-active {
      background: linear-gradient(to top, $egYellow 60%, $white 40%);
    }
  }

  &__container {
    width: 100%;
    display: none;

    &.js-active {
      display: block;
    }
  }
  
  &__media-container {
    width: 100%;
    margin-bottom: -4px;
  }

  &__asset {
    position: relative;
    width: 100%;
  }

  &__media-item {
    position: relative;
    width: 100%;
    height: 0;
    overflow: hidden;
    padding-bottom: 56.5%;
    background: $black;

    img,
    iframe {
      position: absolute;
      top:0;
      left:0;
      right:0;
      bottom:0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      opacity: .7;
    }
  }

  &__media-title {
    position: absolute;
    color: $white;
    border-left: 5px solid $mainColor;
    font-size: 1.2rem;
    font-family: $altFont;
    bottom: .5rem;
    left: .5rem;
    padding-left: .5rem;
    padding-right: .5rem;
    text-transform: uppercase;
    line-height: 1;

    @include mq(md) {
      font-size: 2rem;
      bottom: 1.5rem;
      left: 1.5rem;
      padding-left: 1rem;
    }
  }

  &__slides-container {
    background: #012;
    padding: 1rem 1rem 0;

    .slick-current {
      .eg-gallery__slide-thumb {
        border: 1px solid $white;
        opacity: 1;
      }
    }
  }

  &__slide {
    padding: 0 .5rem;
  }

  &__slide-thumb {
    position: relative;
    width: 100%;
    height: 0;
    overflow: hidden;
    padding-bottom: 56.5%;
    background: $black;
    opacity: .7;

    img,
    iframe {
      position: absolute;
      top:0;
      left:0;
      right:0;
      bottom:0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__slide-title {
    font-size: .8rem;
    color: $white;
    padding-top: 3px;
  }
}