.listado {

  section {
    margin-bottom: 30px;
  }

  &__load {
    border-radius: 50px;
    background: $lightestGray;
    font-family: $altFont;
    text-transform: uppercase;
    font-size: 18px;
    border: none;
    cursor: pointer;
    padding: 5px 15px;
    display: inline-block;
  }

  &__heading {
    border-bottom: 1px solid $lightGray;
    margin-bottom: 30px;

    &__head {
      display: flex;
      align-items: flex-end;      
    }

    &__escudo {
      padding-bottom: 5px;
      img {
        height: 50px;
        width: auto;
        margin-right: 20px;
      }
    }

    &__title {
      color: $darkestGray;
      margin: 20px 0 0;
      position: relative;
      padding-bottom: 20px;
      vertical-align: bottom;
      height: 50px;

      span {
        height: 5px;
        position: absolute;
        bottom: 0;
        left: 0;
        background: $lightGray;

        &:first-child {
          width: 140%;
        }

        &:last-child {
          background: $midGray;
          width: 8%;
          left: calc(140% + 5px);
        }
      }
    }
  }
}