.mas-leidos {
  &__logo {
    position: relative;
    margin: 0 0 5px;
    text-align: center;

    &:before {
      content: '';
      position: absolute;
      background: $egYellow;
      top: calc(50% - 10px);
      height: 20px;
      left: 0;
      right: 0;
      z-index: 0;
    }

    span {
      position: relative;
      z-index: 1;
      background: $white;
      display: inline-block;
      padding: 3px 10px;
      box-shadow: 1px 1px 0 1px $darkestGray;
      font-family: $altFont;
      text-transform: uppercase;
      font-size: 28px;

      &::first-letter {
        color: $mainColor;
      }
    }
  }

  &__articles {
    counter-reset: egCount;
    list-style: none;
    margin: 0 0 15px;
    padding: 0;

    li {
      counter-increment: egCount;
      display: grid;
      grid-template-columns: 24px 1fr;
      padding-top: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid $lightGray;

      a {
        text-decoration: none;
      }

      ._tag {
        font-family: $altFont;
        color: $mainColor;
        font-size: 14px;
        text-transform: uppercase;
        margin: 0;
        display: block;
        letter-spacing: -1px;
      }

      ._title {
        color: $darkestGray;
        font-weight: 500;
        margin: 5px 0 0;
        font-size: 14px;
      }

      &:before {
        content: counter(egCount);
        color: $mainColor;
        font-weight: 700;
        font-size: 21px;
        grid-column: 1;
        grid-row: 1 / span 2;
        line-height: 1;
      }
    }
  }
}
