.home {
  margin: 2rem auto;

  &__main {
    margin-bottom: 20px;
  }

  &__memoria {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    margin-bottom: 20px;

    @include mq(sm) {
      grid-template-columns: 2fr 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      margin-bottom: 30px;
    }

    &__title {
      position: absolute;
      left:30px;
      bottom: 30px;
      z-index: 2;
      font-family: $altFont;
      text-transform: uppercase;
      font-size: 10vw;
      margin: 0;
      text-shadow:
        -2px -2px 0 $egYellow,  
         2px -2px 0 $egYellow,
         -2px 2px 0 $egYellow,
          2px 2px 0 $egYellow;

      @include mq(md) {
        font-size: 5vw;
      }

      @include customMq(1400px) {
        font-size: 72px;
      }
    }

    &__pic {
      overflow: hidden;
      height: 100%;
      position: relative;
      padding-bottom: 80%;

      
      &:nth-child(1) {
        grid-column: 1 / span 2;
        grid-row: 1 / span 2;
        
        @include mq(sm) {
          grid-column: 1;
          grid-row: 1 / span 2;
        }
      }
      &:nth-child(2) {
        grid-row: 3 / span 2;

        @include mq(sm) {
          grid-column: 2;
          grid-row: 1 / span 2;
        }
      }
      &:nth-child(3) {
        grid-column: 2;
        grid-row: 3;

        @include mq(sm) {
          grid-column: 3;
          grid-row: 1;
        }
      }
      &:nth-child(4) {
        grid-column: 2;
        grid-row: 4;

        @include mq(sm) {
          grid-column: 3;
          grid-row: 2;
        }
      }


      img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

.eg-twitter-widget {
  margin-bottom: 25px;
  img {
    width: 100%;
  }
}