.volanta {
  position: relative;
  display: block;
  height: 100%;
  min-height: 480px;

  &__image {
    position: absolute;
    top:0;
    left:0; 
    right:0;
    bottom:0;
    display: block;

    & > img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top:40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
    background: linear-gradient(to top, rgba(0,0,0,.8), transparent 60%);
    text-align: center;

    ._title {
      font-size: 84px;
      color: $egYellow;
      text-shadow: 2px 2px 2px $darkestGray;
      text-transform: uppercase;
      font-family: $altFont;
      margin: 15px 0 10px;
      font-weight: normal;
      letter-spacing: -3px;
    }

    ._subtitle {
      font-weight: 500;
      color: $white;
      font-size: 14px;
      margin: 0;
    }
  }
}