.header {
  $h: &;

  position: sticky;
  top: -1px;
  z-index: 1000;

  &__ad {
    padding: 15px;
    background: $lightestGray;
    display: flex;
    align-items: center;
    justify-content: center;

    &__banner {
      max-width: 468px;
    }
  }

  &__top {
    background: $mainColor;
    display: grid;
    grid-template-columns: 30px 1fr auto;
    gap: 15px;
    padding: 15px;

    @include mq(md) {
      padding-left: 30px;
      padding-right: 30px;
      grid-template-columns: min(180px) 1fr auto;

      #{$h}__hamburguer {
        display: none;
      }
    }
  }

  &__logo {
    @include trans;
    height: 5.5vw;
    max-height: 36px;
    align-self: center;
    justify-self: left;
    grid-column: 2;

    @include mq(md) {
      justify-self: center;
      max-height: 50px;
    }

    img {
      width: 100%;
    }
  }

  &__side {
    display: flex;
    gap: 15px;
    align-self: center;
    align-items: center;

    @include mq(md) {
      gap: 30px;
    }
  }

  &__maradona {
    @include trans;
    height: 7vw;
    max-height: 24px;
    width: auto;

    @include mq(md) {
      max-height: 50px;
    }
  }

  .eg-social {
    padding-left: 15px;
    padding-top: 0;
    padding-bottom: 0;
    border-left: 1px solid rgba(255,255,255,.5);

    @include mq(md) {
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 30px;
      gap: 15px;
    }
  }

  &__bottom {
    @include trans;

    border-top: 1px solid rgba(255,255,255,.3);
    padding-top: 15px;
    padding-bottom: 15px;
    transform: translateX(-100%);
    position: fixed;
    left: 0;
    right:0;
    bottom: 0;
    background: $mainColor;
    overflow: auto;

    @include mq(md) {
      overflow: visible;
      position: static;
      padding-left: 30px;
      padding-right: 30px;
      display: grid;
      grid-template-columns: 30px 1fr 30px;
      grid-template-rows: 1fr;
      transform: translateX(0);
    }

    #{$h}__hamburguer {
      display: none;

      @include mq(md) {
        display: block;
        grid-column: 1;
        margin: 0;
      }
    }

    &::-webkit-scrollbar-track {
      box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
      background-color: #F5F5F5;
    }

    &::-webkit-scrollbar {
      width: 6px;
      background-color: #F5F5F5;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $mainColor;
    }
  }

  &__info {
    color: $white;
    padding-left: 30px;

    @include mq(md) {
      position: absolute;
      top: -65px;
      left: 30px;
      padding-left: 0;
    }
  }

  &__weather {
    display: flex;
    gap: 8px;
    font-size: 0.8rem;
    margin: 0 0 5px;
    align-items: center;
  }

  &__date {
    font-size: 14px;
    margin: 0;
  }

  &__search {
    position: absolute;
    right: 30px;
    top: 30px;
    cursor: pointer;

    @include mq(md) {
      position: static;
      grid-column: 3;
    }
  }

  &__main-menu {
    grid-column: 2;
    grid-row: 1;
    justify-self:center;
    padding-left: 30px;
    padding-top: 30px;
    
    @include mq(md) {
      padding-top: 0;
      padding-left: 0;
      gap: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    li {
      margin-bottom: 10px;

      @include mq(md) {
        margin-bottom: 0;
      }
    }

    a {
      color: $white;
      text-decoration: none;
      font-weight: 500;

      &:hover {
        color: $white;
      }
    }
  }

  &__internal-menu {
    $i: &;

    height: 100%;
    color: $white;
    font-family: $altFont;
    text-transform: uppercase;
    padding-top: 0;

    @include mq(md) {
      padding-top: 20px;
    }

    &__close {
      width: 25px;
      height: 25px;
      background: none;
      border: none;
      position: relative;
      cursor: pointer;
      display: none;
      transform: translateX(-5px);

      @include mq(md) {
        display: block;
      }

      &:after,
      &:before {
        content:'';
        height: 2px;
        background: $white;
        width: 100%;
        display: block;
        position: absolute;
        top:calc(50% - 1px);
        transform: rotate(-45deg);
      }

      &:after {
        transform: rotate(45deg);
      }
    }
    
    &__section {
      color: $midGray;
      margin-bottom: 25px;

      &:last-of-type {
        #{$i}__links {
          padding-bottom: 30px;
          @include mq(md) {
            padding-bottom: 0;
          }
        }
      }
    }

    &__links {
      padding-top: 10px;

      li {
        margin-bottom: 5px;
      }
      a {
        color: $white;
        text-decoration: none;
      }
    }
    
    &__wrapper {
      @include trans;
      padding: 15px 30px 30px 30px;
      overflow: visible;
      
      @include mq(md) {
        overflow: auto;
        background: rgba(0,0,0,.7);
        bottom: 0;
        position: fixed;
        left: 0;
        top: 55px;
        transform: translateX(-100%);
      }

      &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
        background-color: #F5F5F5;
      }

      &::-webkit-scrollbar {
        width: 6px;
        background-color: #F5F5F5;
      }

      &::-webkit-scrollbar-thumb {
        background-color: $mainColor;
      }
    }
  }

  &__search {
    background: none;
    border: none;
    appearance: none;
    color: $white;
  }

  &__hamburguer {
      cursor: pointer;
      width: 25px;
      height: 25px;
      margin: auto;
      background: none;
      border:none;
      appearance: none;
      outline: none;
      padding: 0;

      span {
          background-color: $white;
          border-radius: 2px;
          content: '';
          display: block;
          width: 100%;
          height: 2px;

          &:nth-child(1) {
              animation:outT 0.6s $easing backwards;
              animation-direction:reverse;
          }
          &:nth-child(2) {
              margin: 7px 0;
              animation:outM 0.6s $easing backwards;
              animation-direction:reverse;
          }
          &:nth-child(3) {
              animation:outBtm 0.6s $easing backwards;
              animation-direction:reverse;
          }
      }
  }

  &.-open {
    #{$h}__hamburguer {
      span:nth-child(1) {
        animation:inT 0.6s $easing forwards;
      }
      span:nth-child(2) {
        animation:inM 0.6s $easing forwards;
      }
      span:nth-child(3) {
        animation:inBtm 0.6s $easing forwards;
      }
    }

    #{$h}__bottom {
      transform: translateX(0);
    }

    #{$h}__internal-menu__wrapper {
      @include mq(md) {
        transform: translateX(0);
        box-shadow: 2px 0 15px 5px rgba(0,0,0,.15);
      }
    }
  }

  &.-offtop {
    #{$h}__top {
      #{$h}__hamburguer {
        @include mq(md) {
          display: block;
          justify-self: start;
          margin: auto 0;
        }
      }
    }

    // #{$h}__logo {
    //   @include mq(md) {
    //     max-height: 30px;
    //   }
    // }

    // #{$h}__maradona {
    //   @include mq(md) {
    //     max-height: 30px;
    //   }
    // }

    #{$h}__bottom {
      @include mq(md) {
        transform: translateX(-100%);
        background: $mainColor;
      }

      &.-offtop {
        @include mq(md) {
          position: fixed;
          background: rgba(0,0,0,.7);
          left: 0;
          right:0;
          bottom: 0;
          overflow: auto;
          width: 25%;
          top: 79px !important;
          display: flex;
          flex-direction: column;
          z-index: 2;
          padding-top: 30px;
        }

        #{$h}__hamburguer {
          @include mq(md) {
            display: none;
          }
        }

        #{$h}__info {
          @include mq(md) {
            position: static;
            padding-left: 30px;
          }
        }
  
        #{$h}__search {
          @include mq(md) {
            position: absolute;
            right: 30px;
          }
        }

        #{$h}__main-menu {
          @include mq(md) {
            display: block;
            padding-left: 30px;
            padding-bottom: 0;
            padding-top: 30px;
          }
  
          li:not(:last-child) {
            @include mq(md) {
              margin-bottom: 20px;
            }
          }
        }
    
        #{$h}__internal-menu__wrapper {
          @include mq(md) {
            padding-top: 0;
            position: relative;
            background: none;
            transform: translateX(0);
            box-shadow: none;
            height: auto !important;
            overflow: visible;
          }
        }
  
        #{$h}__internal-menu {
          @include mq(md) {
            padding-top: 0;
          }
        }
      }
    }

    &.-open {
      #{$h}__bottom {
        @include mq(md) {
          transform: translateX(0);
        }        
      }
    }
  }
}

.escudos {
  border-bottom: 1px solid $lightGray;
  box-shadow: 0 2px 5px rgba(0,0,0,.15);
  background: $white;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 5px 10px;
  z-index: 100;
  position: sticky;
  top: 54px;
  overflow: hidden;
  max-height: 50px;
  @include trans;

  &.-expanded {
    max-height: 200px;

    .-expand {
      span:last-of-type {
        width: 0;
      }
    }
  }
    
  @include mq(md) {
    top: 79px;
    padding: 5px 30px;
  }

  li {
    @include mq(xs) {
      order: 3;
    }

    &.-main {
      @include mq(xs) {
        order: 1;
      }
    }

    &.-expand {
      display: none;
      
      @include mq(xs) {
        order:2;
        display: block;
      }

      button {
        width: 38px;
        height: 38px;
        display: flex;
        background: $lightestGray;
        border:none;
        border-radius: 3px;
        align-items: center;
        justify-content: center;
        appearance: none;
        padding: 0;
        position: relative;
        box-shadow: 1px 1px 3px rgba(0,0,0,.15);
      }

      span {
        height: 3px;
        width: 60%;
        position: absolute;
        background: $mainColor;
        display: block;
        @include trans;

        &:last-of-type {
          transform: rotate(90deg);
        }
      }
    }
  }
}

//hamburguer animations
@keyframes inM{
50%{transform:rotate(0deg);}
100%{transform:rotate(45deg);}
}

@keyframes outM{
50%{transform:rotate(0deg);}
100%{transform:rotate(45deg);}
}

@keyframes inT{
0%{transform: translateY(0px) rotate(0deg);}
50%{transform: translateY(9px) rotate(0deg);}
100%{transform: translateY(9px) rotate(135deg);}
}

@keyframes outT{
0%{transform: translateY(0px) rotate(0deg);}
50%{transform: translateY(9px) rotate(0deg);}
100%{transform: translateY(9px) rotate(135deg);}
}

@keyframes inBtm{
0%{transform: translateY(0px) rotate(0deg);}
50%{transform: translateY(-9px) rotate(0deg);}
100%{transform: translateY(-9px) rotate(135deg);}
}
@keyframes outBtm{
0%{transform: translateY(0px) rotate(0deg);}
50%{transform: translateY(-9px) rotate(0deg);}
100%{transform: translateY(-9px) rotate(135deg);}
}
