.nota {
  padding-top: 50px;

  &__heading {
    padding-bottom: 10px;
    position: relative;

    &:before,
    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      height: 5px;
    }

    &:before {
      width: 40%;
      background-color: $mainColor;
      border-color: transparent;
      margin: 0;
    }

    &:after {
      width: 5%;
      background-color: $egYellow;
      border-color: transparent;
      margin: 0;
      left: calc(40% + 5px);
    }

    @include mq(md) {
      width: 90%;
    }

    &__title {
      font-size: 2rem;
      letter-spacing: -2px;
      line-height: 1;
      margin: 2rem 0 1.5rem;
     
      @include mq(md) {
        font-size: 3.5rem;
      }
    }

    &__subtitle {
      color: $midGray;
      font-weight: 500;
      line-height: 1.2rem;
      font-size: 1rem;
    }

    &__author {
      display: flex;
      align-items: center;
      gap: .5rem;
      margin: 1rem 0;

      ._icon {
        width: 32px;
        height: 32px;

        img {
          width: 100%;
        }
      }

      p {
        margin: 0;
        font-size: $xs;
        font-weight: 500;
        text-decoration: underline $midGray;
      }

      span {
        font-size: $xxs;
        color: $midGray;
        &::before {
          content: '';
          margin-right: .25rem;
        }
      }
    }
  }

  &__main {
    

    &-widget {
      height: fit-content;
      margin-bottom: 1.5rem;
    }

    &-side {
      display: flex;
      flex-direction: column;
      justify-self: center;
    }

    &-text {
      p {
        font-size: $sm;
        margin: 0 0 1.5rem 0;
        line-height: 1.5rem;
      }
    }

    &-tags {
      display: flex;
      flex-direction: row;
      gap: .5rem;
      align-items: center;

      &--type1 {
        font-family: $altFont;
        text-transform: uppercase;
        font-size: $xxl;
        text-decoration: underline $mainColor 4px;
        margin-right: 2rem;
      }

      &--type2 {
        font-family: $altFont;
        text-transform: uppercase;
        background-color: $egYellow;
        padding: .2rem .4rem;
      }
    }

    img {
      height: auto;
    }
  }

  &__content {
    padding-bottom: 3rem;
    border-bottom: 1px solid $lightGray;
  }

  &__main-media {
    margin: 1.5rem 0;

    &__container {
      padding-bottom: 56.5%;
      height: 0;
      overflow: hidden;
      position: relative;
      width: 100%;

      iframe,
      img {
        position: absolute;
        width: 100%;
        left:0;
        right:0;
        top:0;
        min-height: 100%;
      }
    }

    &__legend {
      border-bottom: 1px solid $lightGray;
      padding: .5rem 0 .5rem 0;
      font-size: .8rem;
    }
  }

  &__body {
    p {
      margin: 0 0 1rem;
      line-height: 1.5;
    }
  }


  &__image {
    img {
      width: 100%;
    }

    &__legend {
      border-bottom: 1px solid $lightGray;
      padding: .5rem 0 .5rem 0;
      font-size: .75rem;
    }
  }

  &__related {
    border-top: 3px solid $mainColor;
    border-bottom: 1px solid $lightGray;
    display: grid;
    grid-template-columns: 1fr 3fr;
    margin-bottom: 1.5rem;

    &__title {
      color: $mainColor;
      font-family: $altFont;
      text-transform: uppercase;
      margin: 0;
      align-self: center;
    }

    &__articles {
      padding-left: 1rem;
      padding-top: 1rem;
      padding-bottom: 1rem;
      border-left: 1px solid $lightGray;

      li {
        &:not(:last-child) {
          margin-bottom: .75rem;
        }
      }

      a {
        color: $black;
        text-decoration: none;
        font-size: .75rem;
        line-height: 1;
        font-weight: 500;
        display: flex;
        align-items: center;

        &::before {
          content: '';
          width: .5rem;
          height: .75rem;
          background: $egYellow;
          display: inline-block;
          margin-right: .5rem;
        }
      }
    }
  }

  &__quote {
    display: grid;
    gap: 1rem;
    grid-template-columns: min-content 1fr;
    border-right: 2px solid $darkestGray;
    border-bottom: 2px solid $darkestGray;
    margin-bottom: 1.5rem;
    margin-top: 1.5rem;

    ._quote {
      position: relative;
      color: $egPurple;
      font-size: 120px;
      line-height: 0.8;
      align-self: start;
      display: block;
      font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;

      &:before {
        content: '';
        position: absolute;
        left: -.25rem;
        right:-.25rem;
        top: 1.1rem;
        height: 1.3rem;
        background: $egYellow;
        z-index: -1;
      }

      
    }

    p {
      color: $darkestGray;
      text-transform: uppercase;
      font-family: $altFont;
      font-size: 2rem;
      letter-spacing: -2px;
      line-height: 1.1;
    }
  }

  &__infinite {
    padding-bottom: 2rem;
    padding-top: 2rem;

    &__title {
      font-family: $altFont;
      text-transform: uppercase;
      display: inline-block;
      padding: .5rem 1rem .5rem .5rem;
      box-shadow: 1px 1px 0 1px $black;
    }
  }
}

._icon {
  width: fit-content;
  height: fit-content;
}