.eg-scoreboard {
  border-top: 2px solid $mainColor;
  border-bottom: 1px solid $lightGray;
  box-shadow: 0 2px 4px -2px rgba(0,0,0,.15);
  display: grid;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  grid-template-columns: 40px 1fr 40px;
  margin-bottom: 30px;

  &__nav {
    &--left,
    &--right {
      background: none;
      border: none;
      outline: none;
      font-size: 21px;
      display: flex;
      align-items: center;
      height: 100%;
      cursor: pointer;

      &.-disabled {
        pointer-events: none;
        color: $lightGray;
      }
    }

    &--left {
      border-right: 1px solid $lightGray;
    }

    &--right {
      border-left: 1px solid $lightGray;
    }

  }

  &__list {
    display: flex;
  }

  &__section {
    display: flex;
  }

  &__content {
    overflow-x: scroll;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__section-name {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 8px;
    padding-right: 8px;
    border-right: 1px solid $lightGray;
    max-width: 75px;

    ._name {
      background: $egYellow;
      font-family: $altFont;
      font-size: 14px;
      display: inline;
      text-transform: uppercase;
      margin: 0;
    }
  }

  &__match {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    border-right: 1px solid $lightGray;
  }

  &__schedule {
    border-bottom: 1px solid $lightGray;
    grid-row: 1;
    padding: 3px 6px;
    display: flex;
    text-transform: uppercase;
    color: $mainColor;
    align-items: center;

    span {
      font-weight: 500;
      font-size: 10px;
      padding-right: 4px;

      &:not(:first-child) {
        padding-left: 4px;
        border-left: 1px solid $lightGray;
      }
    }
  }

  &__score {
    grid-row: 2 / span 2;
    display: flex;
    flex-direction: column;
    justify-content: center;

    ._teamA,
    ._teamB {
      display: grid;
      gap: 3px;
      grid-template-columns: 16px minmax(60px, 1fr) 20px;
      padding: 3px;
      position: relative;
      justify-self: center;

      img {
        width: 16px;
      }

      span {
        font-weight: 500;
        font-size: 11px;
        align-self: center;
        text-transform: uppercase;

        &.-small {
          font-size: 9px;
          color: $midGray;
          justify-self: center;
        }

        &:nth-child(2) {
          white-space: nowrap;
          padding-right: 5px;
        }

        &:last-of-type {
          justify-self: end;
          padding-right: 5px;
        }
      }

      &.-winner {
        &:after {
          content: '';
          width: 4px;
          height: 4px;
          border-top: 2px solid transparent;
          border-bottom: 2px solid transparent;
          border-right: 4px solid $mainColor;
          position: absolute;
          right:-1px;
          top: calc(50% - 2px);
        }
      }
    }
  }
}