.hemeroteca {
  position: relative;
  border-top: 5px solid $black;
  border-bottom: 5px solid $black;
  margin: 30px 0;

  &__tag {
    position: absolute;
    top:0;
    left: 0;
    z-index: 2;
    background: $egPurple;
    display: inline-block;
    padding: 3px 10px;
    box-shadow: 1px 1px 0 1px $white;
    font-family: $altFont;
    text-transform: uppercase;
    font-size: 18px;
    color: $black;
    letter-spacing: -1px;
    transform: translateY(-45%);
    color: $egYellow;
    font-weight: normal;
    margin: 0;

    @include mq(sm) {
      font-size: 32px;
    }
  }

  &__content {
    display: none;
    position: relative;

    &.-active {
      display: block;
    }
  }

  &__nav {
    padding: 20px 15px 0;

    @include mq(sm) {
      grid-column: 2;
      padding-left: 30px;
      grid-template-columns: 1fr;
      grid-template-rows: repeat(5, 1fr);
      display: grid;
    }
  }

  &__item {
    display: grid;
    padding: 10px 0;
    border-bottom: 1px solid $lightGray;
    grid-template-columns: min-content 4fr;
    gap: 20px;
    cursor: pointer;

    ._year {
      color: $mainColor;
      font-family: $altFont;
      font-size: 32px;
      letter-spacing: -2px;
      align-self: center;
      position: relative;
      display: block;
      font-weight: bold;

      &:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: -3px;
        right: -3px;
        top: 50%;
        background: $egYellow;
        z-index: -1;
      }
    }

    ._title {
      font-family: $altFont;
      font-size: 21px;
      letter-spacing: -1px;
      text-transform: uppercase;
      align-self: center;
      margin: 0;
    }

    &.-active {
      display: none;
    }
  }

  &__image {
    position: relative;
    padding-bottom: 60%;
    margin-bottom: 8px;
    display: block;
    z-index: 1;

    & > img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__data {
    position: absolute;
    bottom: 0;
    z-index: 2;
    left: 20px;
    bottom: 20px;
  }

  &__title {
    background: $white;
    color: $mainColor;
    font-family: $altFont;
    font-size: 5vw;
    text-transform: uppercase;
    margin: 0;
    padding: 3px;
    display: inline;

    @include customMq(1400px) {
      font-size: 45px;
    }
  }

  &__contents {
    margin-bottom: -8px;
    
    @include mq(sm) {
      grid-column: 1;
      grid-row: 1;
    }
  }

  &__wrapper {
    @include mq(sm) {
      display: grid;
      grid-template-columns: 3fr 1.2fr;
    }
  }

  &__year {
    color: $white;
    font-family: $altFont;
    font-weight: bold;
    font-size: 12vw;
    margin-bottom: -5%;
    display: block;

    @include customMq(1400px) {
      font-size: 125px;
      margin-bottom: -3.5%;
      letter-spacing: -5px;
    }
  }
}