.textual {
  margin-bottom: 20px;
  display: block;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    left:0;
    right:0;
    bottom:0;
    top:40%;
    background: linear-gradient(to top, rgba(0,0,0,.85), transparent);
    z-index: 1;
  }

  &__author {
    position: absolute;
    font-weight: 500;
    bottom: 30px;
    left: 30px;
    color: $mainColor;
    margin: 0;
    z-index: 2;
    font-size: 14px;
  }

  &__tag {
    position: absolute;
    top:0;
    left: 0;
    z-index: 2;
    background: $egYellow;
    display: inline-block;
    padding: 3px 10px;
    box-shadow: 1px 1px 0 1px $mainColor;
    font-family: $altFont;
    text-transform: uppercase;
    font-size: 18px;
    color: $black;
    letter-spacing: -1px;
    transform: translateY(-45%);
  }

  &__title {
    font-weight: 700;
    color: $white;
    font-size: 18px;
    position: absolute;
    left:0;
    right:0;
    bottom: 60px;
    
    padding-left: 30px;
    padding-right: 30px;
    margin: 0;
    z-index: 2;
    font-family: $altFont;
    text-transform: uppercase;

    &:before {
      content: '';
      color: $egYellow;
      font-size: 100px;
      line-height: 0;
      display: block;
      font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    }

    &:after {
      content: '';
    }
  }

  &__image {
    position: relative;
    padding-bottom: 140%;
    display: block;
    z-index: 0;
  
    & > img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}