.eg-article {
  $a: &;

  position: relative;
  display: block;

  @include mq(xs) {
    margin-bottom: 30px;
  }

  .eg-tag {
    position: absolute;
    left: .5rem;
    top: .5rem;
  }

  &__image {
    position: relative;
    padding-bottom: 60%;
    margin-bottom: 8px;
    display: block;

    & > img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__heading {
    display: block;
    text-decoration: none;
    padding-bottom: 10px;
    border-bottom: 1px solid $lightGray;
  }

  &__title {
    font-size: 18px;
    margin: 0;
    color: $darkestGray;
  }

  &__subtitle {
    font-size: 14px;
    max-width: 85%;
    font-weight: 500;
    color: $midGray;
  }

  &__score {
    border-radius: 5px;
    background: $lightestGray;
    display: grid;
    padding-right: 15px;
    padding-left: 15px;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 10px;
    text-decoration: none;

    &__team {
      gap: 5px;
      display: flex;
      width: 100%;
      justify-content: flex-end;
      align-items: center;

      ._team {
        order: 1;
        font-family: $altFont;
        padding: 0 8px;
        color: $darkestGray;
      }

      ._badge {
        object-fit: contain;
        max-width: 32px;
        max-height: 32px;
        order: 2;
        mix-blend-mode: multiply;
      }

      ._goals {
        background: $darkestGray;
        color: $white;
        order: 3;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        font-family: $altFont;
        font-size: 18px;
        height: 36px;
        border-radius: 5px 0 0 5px;
      }

      &:last-of-type {
        grid-template-columns: 1fr 38px 7px auto;
        justify-content: flex-start;

        ._team {
          order: 3;
        }
  
        ._badge {
          order: 2;
        }
  
        ._goals {
          order: 1;
          border-radius: 0 5px 5px 0;
          position: relative;

          &:before {
            content: '';
            position: absolute;
            top: 5px;
            bottom:5px;
            left: 0;
            width: 1px;
            background: $darkGray;
          }
        }
      }
    }
  }

  &__related {
    li {
      padding: 10px 0;
      border-bottom: 1px solid $lightGray;
    }

    a {
      text-decoration: none;
      color: $darkestGray;
      font-weight: 500;
      font-size: 14px;
      
      &:before {
        content: '=';
        color: $mainColor;
        margin-right: 8px;
      }
    }
  }

  &--square {
    #{$a}__image {
      padding-bottom: 85%;
    }

    #{$a}__heading {
      @include mq(md) {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        padding-top: calc(85% - 10px);
        display: flex;
        padding-left: 30px;
        padding-right: 30px;
        background: linear-gradient(to top, rgba(0,0,0,.8), transparent 60%);
      }

      &__wrapper {
        @include mq(md) {
          position: absolute;
          left:30px;
          bottom:30px;
          right:30px;
        }

        @include customMq(1330px) {
          max-width: 80%;
        }
      }
    }

    #{$a}__title {
      @include mq(sm) {
        font-family: $altFont;
        text-transform: uppercase;
        font-weight: normal;
        font-size: 3.5vmin;
        letter-spacing: -1px;
        padding: 3px;
      }
      
      @include mq(md) {
        color: $white;
        background: $darkestGray;
        display: inline;
      }

      @include customMq(1330px) {
        font-size: 40px;
      }
    }

    #{$a}__subtitle {
      @include mq(md) {
        display: block;
        color: $white;
      }
    }
  }
}