.eg-tag-cloud {
  display: grid;
  grid-template-columns: min-content 1fr;
  gap: 1.5rem;

  &__title {
    font-size: 2.5rem;
    border-bottom: 5px solid $mainColor;
    font-family: $altFont;
    text-transform: uppercase;
  }

  &__list {
    display: flex;
    gap: .5rem;
    align-self: center;
    align-items: center;
    flex-wrap: wrap;
  }

  &__tag {
    background: $egYellow;
    font-family: $altFont;
    text-transform: uppercase;
    letter-spacing: -0.5px;
    font-size: .85rem;
    padding: 2px 5px;
  }
}