.eg-pos-table {
  margin-bottom: 20px;
  
  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: center;

    a {
      color: $black;
      text-decoration: none;

      &::first-letter {
        color: $mainColor;
      }
    }
  }

  &__selector {
    padding: 10px 0;
  }
  
  &__select {
    width: 100%;
    border-radius: 20px;
    height: 40px;
    border: 1px solid $lightGray;
    padding: 0 15px;
    outline: none;
  }

  &__data {
    & > img {
      width: 100%;
    }
  }

  &__title {
    z-index: 1;
    background: $white;
    display: inline-block;
    padding: 3px 10px;
    box-shadow: 1px 1px 0 1px $darkestGray;
    font-family: $altFont;
    text-transform: uppercase;
    font-size: 18px;
    color: $black;
    letter-spacing: -1px;
  }
}