.opinion {
  margin-bottom: 20px;
  display: block;
  position: relative;

  &__tag {
    position: absolute;
    top:0;
    left: 0;
    z-index: 1;
    background: $white;
    display: inline-block;
    padding: 3px 10px;
    box-shadow: 1px 1px 0 1px $darkestGray;
    font-family: $altFont;
    text-transform: uppercase;
    font-size: 18px;
    color: $black;
    letter-spacing: -1px;
    transform: translateY(-45%);
  }

  &__title {
    font-weight: 700;
    text-decoration: underline;
    color: $white;
    font-size: 18px;
    position: absolute;
    left:0;
    right:0;
    top: 40px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0;
    z-index: 2;
  }

  &__image {
    position: relative;
    padding-bottom: 110%;
    display: block;
  
    & > img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}