.-hide {
    display: none!important;
}

.-hideOverflow {
    overflow: hidden;
}

.-static{
    position: static;
}

.-blur {
    filter: blur(7px);
}

.nopadding > [class*="col-"] {
     padding-left: 0 !important;
     padding-right: 0 !important;
}

.nopadding-xs > [class*="col-"] {
    @include mq(xs){
        padding-left: 0 !important;
        padding-right: 0 !important;
    }
}

.nopadding-selective > .nopad-col {
    padding-left: 0 !important;
    padding-right: 0 !important;
}

.-vertical-space{
    padding-top: $base;
    padding-bottom: $base;

    &-top {
        padding-top: $base;
    }

    &-bottom {
        padding-bottom: $base;
    }
}

.embed-responsive.-map{
    padding-bottom: 56.25%;

    @include mq(sm){
        padding-bottom: 45%;
    }

    @include mq(md){
        padding-bottom: 30%;
    }
}

[data-parallax]{
    position: relative;
}

.eg-tag {
  font-family: $altFont;
  color: $white;
  background: $mainColor;
  padding: 6px 8px 3px 8px;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 1;
}

.text-center {
  text-align: center;
}

.eg-social {
  display: flex;
  gap: 10px;
  align-items: center;
  font-size: 21px;
  align-self: start;

  @include mq(md) {
    padding-bottom: 10px;
    padding-left: 30px;
    gap: 15px;
  }

  svg {
    fill: $darkestGray;
    width: 20px;
    height: 20px;
    margin-top: 2px;
  }

  a {
    color: $darkestGray;
    text-decoration: none;
  }

  &--vert {
    flex-direction: column;
  }

  &--negative {
    a {
      color: $white;
      text-decoration: none;
    }

    svg {
      fill: $white;
    }
  }
}

.eg-capitular {
  position: relative;

  &::first-letter {
    color: $mainColor;
    font-size: 4.5rem;
    float: left;
    line-height: .95;
    margin-right: .75rem;
    font-weight: 900;
    padding-left: .3rem;
    padding-right: .3rem;
    background: linear-gradient(to top, $egYellow 55%, $white 55%);
    box-shadow: 1px 1px 0 1px $black;
  }
}

.eg-widget {
  padding: 1.5rem 0;
  display: flex;
  justify-content: center;

  img {
    max-width: 100%;
  }
}
