.section-ribbon {
  $s: &;

  margin-bottom: 20px;

  &__title {
    position: relative;
    margin: 0 0 5px;
    text-align: center;

    &:before {
      content: '';
      position: absolute;
      background: $mainColor;
      top: calc(50% - 10px);
      height: 20px;
      left: 0;
      right: 0;
      z-index: 0;
    }

    span {
      position: relative;
      z-index: 1;
      background: $white;
      display: inline-block;
      padding: 3px 10px;
      box-shadow: 1px 1px 0 1px $darkestGray;
      font-family: $altFont;
      text-transform: uppercase;
      font-size: 28px;
    }
  }

  &--yellow {
    #{$s}__title:before {
      background: $egYellow;
    }
  }

  &--black {
    #{$s}__title:before {
      background: $black;
    }
  }

  &--small {
    #{$s}__title:before {
      top: calc(50% - 5px);
      height: 10px;
    }
  }
}