.footer {
  background: $darkestGray;
  padding: 30px;

  @include mq(md) {
    padding: 60px;
  }

  &__top {
    @include mq(sm) {
      display: grid;
      grid-template-columns: auto 1fr auto;
      gap: 100px;
    }
  }

  &__logo {
    max-width: 200px;
    margin-bottom: 30px;
    align-self: start;
    
    img {
      width: 100%;
    }
  }

  &__menu {
    @include mq(sm) {
      display: flex;
      flex-wrap: wrap;
      gap: 60px;
      align-self: start;
    }
  }

  &__menu-section {
    margin-bottom: 20px;

    &__title {
      color: $white;
      font-family: $altFont;
      text-transform: uppercase;
      font-weight: normal;
      margin: 0 0 10px;

      @include mq(md) {
        margin: 0 0 20px;
      }
    }
  }

  &__menu-links {
    li {
      margin-bottom: 5px;
    }
    a {
      color: $midGray;
      text-decoration: none;
    }
  }

  &__bottom {
    padding-top: 30px;

    @include mq(md) {
      display: flex;
      justify-content: space-between;
    }
  }

  &__copy {
    color: $midGray;
    font-size: 12px;
  }

  &__credits {
    max-width: 100px;
    display: block;

    img {
      width: 100%;
      height: auto;
    }
  }
}
